* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-card {
  display: flex;
}

.main-title {
  font-weight: bold;
  margin: 0;
  padding-left: 10px;
}

.country-info p {
  font-size: 19px;
}
.card-text {
  font-weight: bold;
  font-size: 19px;
}

.flag {
  width: 30px;
  object-fit: contain;
}
.country-list {
  padding: 0;
  margin: 0;
}
.country-item {
  list-style: none;
  display: flex;
  /* align-items: flex-start; */
  padding-bottom: 10px;
}

.title {
  margin: 0;
  padding-left: 10px;
  font-size: 25px;
}
